@charset "UTF-8";

html{
  width: 100%;
  background-color: #fff;
}
body {
  img{
    max-width: 100%;
    vertical-align: bottom;
  }
  a{
    &:hover{
      opacity: .8;
    }
  }
}
#collaboration18{
  .pc{
    @include PC {display: block;}
    @include SP {display: none;}
  }
  .sp{
    @include PC {display: none;}
    @include SP {display: block;}
  }
  .block{
    @include PC {padding-top: 100px;}
    @include SP {padding-top: 25px;}
  }
  h2{
    text-align: center;
    margin: auto;
    @include PC {margin-bottom: 10px;}
    @include SP {margin-bottom: 5px;}
    +p{
      text-align: center;
      color: rgb(165, 74, 82);
      @include PC {font-size: 22px;}
      @include SP {font-size: 13px;}
    }
  }
  .color01{
    background-color: rgb(210, 184, 175);
    h2+p{
      color: #fff;
    }
  }
  .imgWrap{
    margin: auto;
    position: relative;
    @include PC {
      width: 1000px;
    }
    // overflow: hidden;
  }
  .voiceWrap{
    text-align: center;
    color: rgb(126, 67, 71);
    background-color: #fff;
    @include PC {
      width: 542px;
      font-size: 20px;
      padding: 20px 0 50px;
    }
    @include SP {
      width: 88%;
      font-size: 11px;
      line-height: 1.8em;
      padding: 5px 0 20px;
      right: 0;
      left: 0;
      margin: auto;
      position: relative !important;
      dt{
        width: 53.75%;
        margin: auto;
      }
    }
    &:before,
    &:after{
      content: "";
      display: block;
      position: absolute;
      background-color: #926868;
      @include PC {bottom: -20px;}
      @include SP {bottom: -10px;}
    }
    &:before{
      @include PC {
        width: 20px;
        height: 240px;
      }
      @include SP {
        width: 10px;
        height: 130px;
      }
    }
    &:after{
      width: 90%;
      @include PC {height: 20px;}
      @include SP {height: 10px;}
    }
    &.right{
      &:before{
        @include PC {right: -20px;}
        @include SP {right: -10px;}
      }
      &:after{
        @include PC {right: -20px;}
        @include SP {right: -10px;}
      }
    }
    &.left{
      @include PC {left: 20px;}
      @include SP {left: 10px;}
      &:before{
        @include PC {left: -20px;}
        @include SP {left: -10px;}
      }
      &:after{
        @include PC {left: -20px;}
        @include SP {left: -10px;}
      }
    }
    dt{
      margin-bottom: 3px;
      img{
        @include PC {margin-left: -50px;}
        @include SP {margin-left: -11%;}
      }
    }
  }
  .itemImg{
    display: flex;
    @include PC {margin-top: 100px;}
    // @include SP {margin-top: 65%;}
    li{
      @include PC {font-size: 18px;}
      @include SP {font-size: 13px;}
      &:first-child{
        @include PC {margin-right: 70px;}
        @include SP {margin-right: 2%;}
      }
    }
  }
  .itemInfo{
    margin: auto;
    overflow: hidden;
    @include PC {width: 520px;}
    @include SP {width: 85%;}
    .itemSpec{
      float: left;
    }
    .button{
      float: right;
      margin-top: 4px;
      @include SP {
        width: 37%;
      }
    }
  }
  .cartWrap{
    margin: auto;
    li{
      line-height: 1.5em;
      color: rgb(52, 52, 52);
      @include PC {
        font-size: 18px;
      }
      @include SP {font-size: 11px;}
    }
    .itemImg{
      @include SP {margin: auto;}
    }
  }
  // button非表示のとき
  .btnnone{
    .itemSpec{
      text-align: center;
      float: none;
    }
    .button{display: none;}
  }
  .slide{
    position: relative;
    height: auto;
    &:before{
      @include SP {
        content: "";
        display: block;
        padding-top: 137%;
      }
    }
    li{
      position: absolute;
      @include SP {
        top: 0; 
        left: 0;
      }
      img{
        transition-duration: 2.5s;
        opacity: 0;
      }
      &.active{
        img{
          transition-duration: 1.5s;
          opacity: 1;
        }
      }
    }
  }
}
