#collaboration18{
  font-family: "游ゴシック", "Yu Gothic", YuGothic, -apple-system, BlinkMacSystemFont,"Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ ゴシック", sans-serif;
  position: relative;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  background-color: rgb(255, 244, 242);
  color: rgb(51, 51, 51);
  @include PC {min-width: 1000px;}
  .header{
    text-align: center;
    
    background-color: #fff;
    @include PC {padding: 34px 0;}
    @include SP {
      line-height: 1em;
      padding: 13px 0 14px;
      img{
        width: 123px;
      }
    }
  }
  .main{
    h1{
      max-width: 1292px;
      margin: auto;
      text-align: center;
    }
    .txt{
      line-height: 1.8em;
      text-align: center;
      color: rgb(168, 85, 89);
      @include PC {
        font-size: 24px;
        padding: 90px 0;
      }
      @include SP {
        font-size: 13px;
        padding: 30px 0 40px;
      }
    }
  }
  .sec1{
    @include PC {padding-bottom: 180px;}
    @include SP {padding-bottom: 50px;}
    h2{
      @include SP {width: 71.875%}
    }
    .slideAni01{
      @include PC {
        width: 570px;
        height: 772px;
      }
      @include SP {width: 65.31%; }
    }
    .slideAni02{
      @include PC {width: 377px;}
      @include SP {width: 51.56%;}
    }
    .imgWrap{
      @include PC {margin-top: 100px;}
      @include SP {margin-top: 25px;}
      .rightPos01{
        position: absolute;
        right: 0;
        @include PC {top: 380px;}
        @include SP {top: 23%;}
      }
      .leftPos01{
        @include PC {margin: 90px 0 0 70px;}
        @include SP {
          width: 35%;
          margin: 6% 0 0 6%;
        }
      }
      .voiceWrap{
        position: absolute;
        @include PC {
          top: 950px;
          right: 0px;
        }
        @include SP {
          top: 105%;
          margin-top: 6%;
        }
      }
    }
    .cartWrap{
      @include PC {width: 650px;}
      @include SP {
        margin-top: 40px;
      }
      .itemImg{
        margin-bottom: 4%;
        @include SP {width: 90%;}
      }
    }
  }
  .sec2{
    @include PC {padding-bottom: 140px;}
    @include SP {padding-bottom: 70px;}
    h2{
      @include SP {width: 85.31%;}
    }
    .imgWrap{
      @include PC {margin-top: 40px;}
      @include SP {
        margin-top: 30px;
      }
      .rightPos01{
        @include PC {
          position: absolute;
          top: 500px;
          right: 0;
        }
        @include SP {
          width: 89%;
          margin: 20px 0;
          margin-left: 11%;
        }
      }
      .voiceWrap{
        @include PC {margin-top: 280px;}
      }
    }
    .photo{
      display: flex;
      @include PC {margin: 70px 0 0 200px;}
      @include SP {
        width: 85.93%;
        margin: auto;
        margin-top: 50px;
      }
      li{
        &:first-child{
          @include PC {margin: 70px 80px 0 0;}
          @include SP {
            width: 40%;
            margin-right: 8%;
            margin-top: 10%;
          }
        }
        &:last-child{
          width: 53%;
        }
      }
    }
    .cartWrap{
      @include PC {width: 580px;}
      @include SP {
        margin-top: 30px;
      }
      .itemImg{
        @include PC {margin-bottom: 70px;}
        @include SP {
          width: 75%;
          margin-bottom: 20px;
        }
      }
    }
  }
  .sec3{
    @include PC {padding-bottom: 150px;}
    @include SP {padding-bottom: 70px;}
    h2{
      @include SP {width: 72.34%;}
    }
    .imgWrap{
      @include PC {margin-top: 50px;}
      @include SP {margin-top: 30px;}
      .photo{
        @include PC {overflow: hidden;}
        .leftPos01{
          @include PC {margin-left: 90px;}
          @include SP {
            width: 58%;
          }
        }
        .rightPos01{
          @include PC {
            position: absolute;
            top: 240px;
            right: 90px;
          }
          @include SP {
            width: 38.43%;
            margin-left: 61.57%;
            margin-top: -47%;
          }
        }
        .leftPos02{
          z-index: 1;
          @include PC {
            position: absolute;
            left: 200px;
            top: 720px;
          }
          @include SP {
            width: 40.46%;
            margin-top: -40px;
            margin-left: 6%;
            position: relative;
          }
        }
        .rightPos02{
          @include PC {
            float: right;
            margin-right: 90px;
            margin-top: 170px;
          }
          @include SP {
            width: 50.62%;
            margin-right: 6%;
            margin-left: 43.38%;
            margin-top: -41%;
          }
        }
      }
    }
    .voiceWrap{
      margin: auto;
      @include PC {margin-top: 70px;}
      @include SP {margin-top: 10%;}
    }
    .cartWrap{
      @include PC {width: 770px;}
      .itemImg{
        @include PC {margin-bottom: 70px;}
        @include SP {
          width: 91%;
          margin-top: 10%;
          margin-bottom: 5%;
        }
      }
    }
  }
  .sec4{
    @include PC {padding-bottom: 160px;}
    @include SP {padding-bottom: 90px;}
    h2{
      @include SP {width: 28.12%;}
    }
    .slideAni03{
      @include PC {
        width: 389px;
        height: 563px;
      }
    }
    .imgWrap{
      @include PC {
        margin-top: 30px;
        padding-bottom: 20px;
      }
      @include SP {
        margin-top: 25px;
      }
      .photo{
        @include PC {margin-left: 80px;}
        @include SP {
          width: 73.12%;
        }
      }
      .slide{
        &:before{
          padding-top: 144%;
        }
      }
      .rightPos01{
        @include PC {
          position: absolute;
          right: 90px;
          top: 240px;
        }
        @include SP {
          width: 60.62%;
          margin-left: 39.38%;
          margin-top: -40%;
        }
      }
      .voiceWrap{
        @include PC {margin-top: 70px;}
        @include SP {margin-top: 8%;}
      }
    }
    .cartWrap{
      @include PC {width: 660px;}
      @include SP {margin-top: 10%;}
      .itemImg{
        @include PC {margin-bottom: 70px;}
        @include SP {
          width: 85.46%;
          margin-bottom: 6%;
        }
      }
    }
  }
  .sec5{
    @include PC {padding-top: 50px;}
    @include SP {padding-top: 25px;}
    h2{
      @include SP {width: 46.87%;}
    }
    h2{
      margin-bottom: 20px;
    }
    p{
      width: 89.53%;
      margin: auto;
    }
    .txt{
      margin: auto;
      letter-spacing: .05em;
      @include PC {
        width: 720px;
        font-size: 19px;
        margin-top: 30px;
      }
      @include SP {
        width: 86%;
        font-size: 10px;
        margin-top: 5%;
        line-height: 1.5em;
      }
    }
    .proWrap{
      margin: auto;
      @include PC {
        width: 800px;
        padding-bottom: 60px;
      }
      @include SP {
        width: 90%;
        padding-bottom: 30px;
        line-height: 1.2em;
      }
      dl{
        background-color: #fff;
        @include PC {
          font-size: 20px;
          padding: 40px 50px;
          margin-top: 70px;
        }
        @include SP {
          font-size: 11px;
          padding: 20px 20px;
          margin-top: 25px;
        }
        dt{
          @include PC {margin-top: -64px;}
          @include SP {
            width: 18.5%;
            margin-top: -3em;
          }
        }
        dd{
          margin-top: 1em;
        }
      }
    }
  }
  .sec6{
    text-align: center;
    @include PC {padding: 120px 0 80px 0;}
    @include SP {padding: 80px 0 30px 0;}
    .sns{
      margin: auto;
      overflow: hidden;
      color: rgb(124, 77, 81);
      @include PC {
        width: 260px;
        font-size: 23px;
        margin-top: 140px;
      }
      @include SP {
        width: 140px;
        font-size: 12px;
        margin-top: 70px;
      }
      dt{
        @include PC {margin-bottom: 20px;}
        @include SP {margin-bottom: 10px;}
      }
      dd{
        float: left;
        @include PC {margin-right: 42px;}
        @include SP {
          width: 25%;
          margin-right: 12%;
        }
        &:last-child{
          margin-right: 0;
        }
        &:first-child{
          @include SP {margin-top: 4px;}
        }
      }
      // display: block;
    }
  }
  .footer{
    text-align: center;
    background-color: rgb(124, 77, 81);
    @include PC {padding: 70px 0 10px;}
    @include SP {
      text-align: center;
      padding: 20px 0 10px;
    }
    p{
      @include SP {
        margin: auto;       
      }
      &:first-child{
        @include PC {margin-bottom: 60px;}
        @include SP {
          width: 38.43%;
          line-height: 1em;
          margin-bottom: 20px;
        }
      }
      &:last-child{
        @include SP {
          width: 51.56%;
          line-height: .6em;
        }
      }
    }
  }
}
